@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-sticky {

	// buttons
	--dpit-btn-tx-color: var(--accent-contrast-inv-color);
	--dpit-btn-tx-on-color: var(--accent-contrast-color);
	--dpit-btn-bg-color: var(--accent-color);
	--dpit-btn-bg-alpha: 0.3;

	// number
	//--dpit-number-tx-color: var(--accent-contrast-color);


	// leave default for mobiles
	@include atHoverAbleDev {
		.dpit-scrollbar {
			&:not(.dpit-tooltip-text) {

				@media (min-width: 801px) {
					scrollbar-color: rgba(var(--accent-contrast-color), 0.9) rgba(var(--accent-contrast-inv-color), 0.2);

					&::-webkit-scrollbar-track {
						margin: 8vw;
					}

					&::-webkit-scrollbar-thumb {
						background-color: rgba(var(--accent-contrast-color), 0.9);
						border: 1px solid rgba(var(--accent-contrast-color), 0.8);
					}

					&::-webkit-scrollbar-track {
						background-color: rgba(var(--accent-contrast-inv-color, 0.2));
						border: 1px solid rgba(var(--accent-contrast-color), 0.5);
					}
				}
			}

			&.dpit-tooltip-text {

				&::-webkit-scrollbar-thumb {
					border-radius: 0 var(--button-border-radius) var(--button-border-radius) 0;
				}

				&::-webkit-scrollbar-track {
					border-radius: 0 var(--button-border-radius) var(--button-border-radius) 0;
				}
			}


		}
	}


	.dpit-arrow {
		display: none !important;
	}


	.dpit-tooltip {
		@include z-index(5);
		position: fixed;
		min-width: unset;
		max-width: 100vw;
		bottom: 0;
		left: 0 !important;
		top: unset !important;
		right: 0 !important;
		border-top: 4px solid rgb(var(--accent-color));
		padding: unit(45) unit(50) unit(35);

		border-radius: 0;

		&--with-progress-bar {
			border-top-width: 0;
		}

		&--top {
			bottom: initial !important;
			top: 0 !important;
			border-top-width: 0;
			border-bottom: 4px solid rgb(var(--accent-color));

			&.dpit-tooltip--with-progress-bar {
				border-bottom-width: 0;
			}
		}

		@include atXS {
			padding: unit(30) unit(60);
		}



	}

	.dpit-tooltip-radius-padding {
		display: flex;
		align-items: stretch;
		padding: 0;

		@include atS {
			flex-direction: column;
		}
	}

	.dpit-tooltip-text {
		padding: 0 unit(40) unit(50);
		min-height: unit(110);
		flex: 1;
		max-height: 35vh;

		@include atS {
			min-height: unset;
			padding: 0;
		}

		overflow-y: auto;
		margin: 0;

		@include atXS {
			min-height: unset;
		}


		@media ((orientation: landscape) and (max-height: 480px)) {
			max-height: 15vh;
		}
	}


	.dpit-wrap {
		&--dummy {
			.dpit-number {
				opacity: 0;
				pointer-events: none;
				transition: none;
			}

			.dpit-tooltip-text {
				max-height: unset;
			}
		}
	}


	.dpit-button-pn-wrap {
		@include atS {
			flex-direction: row;
			gap: 0;
		}
	}


	@media (min-width: 801px) {

		.dpit-tooltip-buttons {
			padding-top: 0;
			padding-bottom: 0;
			min-height: 100%;
			flex: 0;
			gap: unit(40);
			flex-direction: column;
			justify-content: space-between;
			align-items: flex-end;
		}

		.dpit-button {
			border: 1px solid rgba(var(--accent-contrast-inv-color), 0.3);

			&--skip {
				margin-right: 0;
				border: 1px solid rgba(var(--accent-contrast-inv-color), 0.35);
				border-radius: var(--button-border-radius);
				@include transitionMulti($transition-time-faster-x, ease-in, background-color, width, border);

				&:not(.dpit-button--done) {

					&:not(.dpit-disabled) {

						background-color: rgba(var(--accent-color), 0.15);

						&:focus {
							background-color: rgba(var(--accent-color), 0.15);
						}

						@include atHoverAbleDev {

							&:active,
							&:hover,
							&.dpit-hovered {
								background-color: rgba(var(--accent-color), 0.20);
								border: 1px solid rgba(var(--accent-contrast-inv-color), 0.55);

								.dpit-button__ico-wrap {
									color: rgb(var(--accent-contrast-inv-color));
								}
							}
						}
					}

					width: unit(84);
					height: unit(84);
					padding: unset;

					.dpit-button__ico-wrap {
						margin-right: 0;
						color: rgba(var(--accent-contrast-inv-color), 0.95);
					}

					.dpit-button__text {
						display: none;
					}

					&:focus {
						background-color: rgba(var(--accent-color), 0.25);
					}
				}
			}
		}

		.dpit-bullets {
			position: fixed;
			top: unit(20);
			left: 50%;
			transform: translate(-50%, 0);
			@include transitionFast(opacity);

			ul {
				max-width: 70vw;
				gap: 0.7vw;
				padding: 0.7vw 0.35vw 0.3vw;

				&.dpit-has-scrollbar {
					padding-bottom: unit(25);
				}

				li {
					padding: 1vw;

					background-color: rgba(var(--accent-contrast-color), 0.7);
					outline: 1px solid rgba(var(--accent-contrast-inv-color), 0.7);
					@include transitionFast(transform, background-color, outline);

					>div {
						color: rgba(var(--accent-contrast-color), 0.95);
					}

					&.active,
					&:active,
					&:hover {
						background-color: rgb(var(--accent-color-gentle));
						outline: 2px solid rgba(var(--accent-contrast-color), 0.9);

						>div {
							color: rgb(var(--accent-contrast-color));
							display: block;
						}
					}

					@include atM {
						padding: unit(25);
					}
				}

				@include atM {
					max-width: 80vw;
					gap: unit(20);

					&.dpit-has-scrollbar {
						padding-bottom: unit(35);
					}
				}
			}
		}


		.dpit-tooltip--top {
			.dpit-bullets {
				top: unset;
				bottom: unit(30);
			}
		}

	}

	.dpit-progress {
		position: absolute;
		box-sizing: border-box;
		width: 100%;
		left: 0;
		top: 0;
		height: unit(16);
		margin: 0;
		border-radius: 0;
		border-top: unit(4) solid rgba(var(--accent-contrast-color), 0.9);
	}

	.dpit-tooltip--with-progress-bar.dpit-tooltip--top {
		.dpit-progress {
			top: unset;
			bottom: 0;
		}
	}

	.dpit-progress-bar {
		background-color: rgba(var(--accent-color), 0.767);
	}


	/* SECTION LIGHT ACCENT */
	&.dpit-light-accent {
		@media (min-width: 801px) {
			.dpit-bullets {
				ul {
					li {
						background-color: rgb(var(--accent-contrast-color-gentle));
						outline: 0px solid rgb(var(--accent-contrast-color));

						>div {
							color: rgb(var(--accent-contrast-color));
						}

						&:active,
						&.active,
						&:hover {
							outline: 2px solid rgba(var(--accent-contrast-color), 0.9);
							background-color: rgb(var(--accent-color));
						}

					}
				}
			}
		}
	}

	/* !SECTION LIGHT ACCENT */
}